<template>
    <v-container>
      <h1>Change Case</h1>
      <v-row>
        <v-col
          cols="12"
        >
          <v-textarea 
            v-model="posgrestInfo"
            label="Posgrest Info"
            @change="onPosgrestInfoChange()"
          ></v-textarea>
        </v-col>
      </v-row>
      <v-row>
        <v-col
          cols="6"
        >
          <v-textarea
            v-model="camelText"
            label="Camel Case"
          ></v-textarea>
        </v-col>
        <v-col
          cols="6"
        >
          <v-textarea
            v-model="snackText"
            label="Snack Case"
          ></v-textarea>
        </v-col>
      </v-row>
      <v-row>
        <v-col
          cols="6"
        >
          <!-- <v-textarea
            v-model="camelTextOnly"
            label="Camel Case Only"
          ></v-textarea> -->
        </v-col>
        <v-col
          cols="6"
        >
          <v-textarea
            v-model="snakeTextOnly"
            label="Snack Case Only"
          ></v-textarea>
        </v-col>
      </v-row>
    </v-container>
</template>

<script>
import {
    camelCase,
    snakeCase,
//   capitalCase,
//   constantCase,
//   dotCase,
//   headerCase,
//   noCase,
//   paramCase,
//   pascalCase,
//   pathCase,
//   sentenceCase,
} from "change-case";

export default {
    data() {
        return {
            posgrestInfo : "",
            camelText : "",
            snackText : "",
            snakeTextOnly : "",
        };
    },
    methods : {
        onPosgrestInfoChange(){
            let line = [];
            var txt = this.posgrestInfo;
            if(txt.indexOf("\n")>0){
                line = txt.split("\n");
            }

            let tmpCamelText = "";
            let tmpSnakeText = "";
            let tmpSnakeTextOnly = "";
            let newLine = "\n";

            console.log("line.length : " + line.length);

            for(let i=0; i<line.length; i++){
// "MasterGroupId" as master_group_id
                if(line[i].indexOf(', "')>=0){
                    tmpSnakeText += line[i] + " as " + snakeCase(line[i].replace(', "','').replace('"',"")) + newLine;
                    tmpSnakeTextOnly += snakeCase(line[i].replace(', "','').replace('"',"")) + newLine;
                } else {
                    let nullable = "true";
                    let dbType = "";
                    let programType = "string";

                    // console.log("i : " + line[i]);

                    let part = line[i].split("	");

                    // console.log("part.length : " + part.length);

                    //Check Type
                    dbType = `type: "${part[2]}",`;

                    if(part[2].indexOf("int")>=0){
                        programType = "number";
                        tmpSnakeText += "@IsNumber()" + newLine;
                    } else if(part[2].indexOf("varchar")>=0){
                        programType = "string";
                        dbType = "";
                    } else if(part[2].indexOf("uuid")>=0){
                        programType = "string";
                        tmpSnakeText += "@IsUUID('4')" + newLine;
                    } else if(part[2].indexOf("timestamp")>=0){
                        programType = "Date";
                        tmpSnakeText += "@Validate(isDateTimeWithTimeZone)" + newLine;
                    } else if(part[2].indexOf("boolean")>=0){
                        programType = "boolean";
                    } else if(part[2].indexOf("json")>=0){
                        programType = "object";
                    }

                    //Check is can NULL
                    if(part[5]=="true"){
                        nullable = "false";
                        tmpCamelText += "@IsNotEmpty()" + newLine;
                        tmpSnakeText += "@IsNotEmpty()" + newLine;
                    } else {
                        tmpSnakeText += "@IsOptional()" + newLine;
                    }

                    //CAMEL Case
                    tmpCamelText += `@Column({${dbType} name: "${part[0]}", nullable: ${nullable}})` + newLine;
                    tmpCamelText += `${camelCase(part[0])}: ${programType}` + newLine;
                    tmpCamelText += newLine;  

                    //SNACK Case
                    tmpSnakeText += `${snakeCase(part[0])}: ${programType}` + newLine;
                    tmpSnakeText += newLine;

                    // updated_by_id: string

                }

            }

            // console.log("tmpSnakeText : " + tmpSnakeText);

            this.camelText = tmpCamelText;
            this.snackText = tmpSnakeText;
            this.snakeTextOnly = tmpSnakeTextOnly;
        }
    }

}
</script>

